body {
	color: rgb(63, 59, 52);
}

h1 {
	color: rgb(63, 59, 52);
	font-size: 2rem;
	margin: 0px;
}
h4 {
	color: rgb(63, 59, 52);
	margin: 0;
}

.subtitle1 {
	color: rgba(63, 59, 52, 0.5);
	font-weight: bold;
	font-style: italic;
	font-size: 0.7rem;
	text-align: center;
	margin: 0;
}

.App {
	height: 100vh;
	width: 100vw;
	background-image: url("./assets/profile-image-min.jpg");
	filter: grayscale(100%);
	background-size: contain;
	background-repeat: no-repeat;
	background-position-x: center;
	background-position-y: center;
	/*
	animation: 1s ease-out 0s 1 fadeIn;
	-webkit-animation: 1s ease-out 0s 1 fadeIn;
	-o-animation: 1s ease-out 0s 1 fadeIn;
	-moz-animation: 1s ease-out 0s 1 fadeIn;
	overflow: hidden;
	*/
}

.App-inner {
	height: 100vh;
	width: 100vw;
	background: radial-gradient(
		circle,
		rgba(187, 174, 149, 0) 0%,
		rgb(187, 174, 149) 35%
	);
	background: -webkit-linear-gradient(
		circle,
		rgba(187, 174, 149, 0) 0%,
		rgb(187, 174, 149) 35%
	);
}

.App-inner-inner {
	height: 100vh;
	width: 100vw;
	background: linear-gradient(
		90deg,
		rgba(187, 174, 149, 0) 48%,
		rgba(187, 174, 149, 1) 52%
	);
	background: -webkit-linear-gradient(
		left,
		rgba(187, 174, 149, 0) 48%,
		rgba(187, 174, 149, 1) 52%
	);
	/*
	animation: 2s ease-out 0s 1 fadeIn;
	-webkit-animation: 2s ease-out 0s 1 fadeIn;
	-o-animation: 2s ease-out 0s 1 fadeIn;
	-moz-animation: 2s ease-out 0s 1 fadeIn;
	*/
}

.divider {
	height: 1px;
	background-color: rgb(63, 59, 52);
	width: 70%;
}

.app-bar {
	background-color: rgb(187, 174, 149);
	width: 100vw;
	height: 70px;
	border-bottom-width: 1px;
	border-bottom-color: rgb(187, 161, 112);
	border-bottom-style: solid;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 16px 16px;
	/*
	animation: 1s ease-out 0s 1 fadeIn;
	-webkit-animation: 1s ease-out 0s 1 fadeIn;
	-o-animation: 1s ease-out 0s 1 fadeIn;
	-moz-animation: 1s ease-out 0s 1 faseIn;
	*/
}

.app-bar-nav {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.app-bar-nav:last-child {
	padding-right: 0px;
}

.app-bar-nav-item {
	cursor: pointer;
	background-color: transparent;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding-left: 8px;
	padding-right: 8px;
	transition: background-color 200ms linear;
	-webkit-transition: background-color 200ms linear;
	-o-transition: background-color 200ms linear;
	-moz-transition: background-color 200ms linear;
	border-radius: 4px;
	color: rgb(63, 59, 52);
}

.app-bar-nav-item:hover {
	background-color: rgba(63, 59, 52, 0.07);
}

.app-bar-nav-item > .nav-image {
	width: 30px;
	height: 25px;
	color: rgb(63, 59, 52);
}

.home {
	width: 100vw;
	height: calc(100vh - 70px);
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	color: rgb(63, 59, 52);
}

.home-grid-item {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.home-welcome {
	width: 70%;
	font-size: 2.5rem;
	font-weight: bold;
	text-align: center;
}

.home-description {
	width: 60%;
	font-size: 1.1rem;
	font-weight: 200;
	text-align: center;
}

.example-enter {
	opacity: 0;
}

.example-enter.example-enter-active {
	opacity: 1;
	transition: opacity 200ms ease-in;
	-webkit-transition: opacity 200ms ease-in;
	-o-transition: opacity 200ms ease-in;
	-moz-transition: opacity 200ms ease-in;
}

.example-exit {
	opacity: 1;
}

.example-exit.example-exit-active {
	opacity: 0;
	transition: opacity 200ms ease-in;
	-webkit-transition: opacity 200ms ease-in;
	-o-transition: opacity 200ms ease-in;
	-moz-transition: opacity 200ms ease-in;
}

.contactPage-root {
	background-color: rgba(187, 174, 149, 1);
	width: 100vw;
	height: calc(100vh - 70px);
	padding: 16px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.contactPage-card-container {
	border: 1px solid;
	border-color: rgb(187, 161, 112);
	border-radius: 8px;
	padding: 16px;
}

.contactPage-card-container > h4 {
	margin-top: 12px;
	margin-bottom: 8px;
	border-bottom: 1px solid rgb(187, 161, 112);
}
.contactPage-card-headerRow {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
}
.contactPage-card-headerRow-name {
	margin-left: 16px;
}

.contactPage-card-divider {
	height: 1px;
	background-color: rgb(187, 161, 112);
	margin-top: 16px;
	margin-bottom: 16px;
}

.contactPage-card-detailRow {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
}

.contactPage-card-icon {
	color: rgb(63, 59, 52);
	margin-right: 16px;
}

.contactPage-card-downloadButton {
	color: rgb(63, 59, 52);
	background-color: transparent;
	border: none;
	box-shadow: none;
	transition: all 50ms linear;
	-webkit-transition: all 50ms linear;
	-o-transition: all 50ms linear;
	-moz-transition: all 50ms linear;
	border-bottom-left-radius: 2px;
	border-bottom-right-radius: 2px;
}

.contactPage-card-downloadButton:hover {
	background-color: rgb(187, 161, 112);
	scale: 1;
}

.contactPage-card-downloadButton:active {
	background-color: rgb(63, 59, 52);
	scale: 0.93;
	border-top-right-radius: 2px;
	border-top-left-radius: 2px;
	color: rgb(187, 161, 112);
}

@media (pointer: coarse) {
	.contactPage-card-downloadButton {
		background-color: rgb(187, 161, 112);
	}
	.contactPage-card-downloadButton:hover {
		background-color: rgb(187, 161, 112);
	}
	.contactPage-card-downloadButton:active {
		background-color: rgb(187, 161, 112);
		scale: 0.93;
		border-top-right-radius: 2px;
		border-top-left-radius: 2px;
		color: rgb(63, 59, 52);
	}
}

@keyframes fadeIn {
	0% {
		opacity: 0%;
	}
	100% {
		opacity: 100%;
	}
}

@-webkit-keyframes fadeIn {
	0% {
		opacity: 0%;
	}
	100% {
		opacity: 100%;
	}
}

@-o-keyframes fadeIn {
	0% {
		opacity: 0%;
	}
	100% {
		opacity: 100%;
	}
}

@-moz-keyframes fadeIn {
	0% {
		opacity: 0%;
	}
	100% {
		opacity: 100%;
	}
}

@keyframes translateInFromRight {
	0% {
		transform: translateX(500px);
	}
	50% {
		transform: translateX(500px);
	}
	100% {
		transform: translateX(0);
	}
}
